import React, { Component } from 'react';
import styled from 'styled-components';

import { Switch, Checkbox } from './reactComponentLib';

const StyledDiv = styled.div`
  padding: 25px;
`;

const Columns = styled.div`
  display: flex;
  > * {
    margin-right: 25px;
  }
`;

class App extends Component {
  render() {
    console.log(this.state);
    return (
      <div>
        <StyledDiv>
          <h1>Switch</h1>
          <Columns>
            <Switch />
            <Switch disabled />
            <Switch initialValue={true} disabled />
            <Switch initialValue={true} />
            <Switch on="On" off="Off" />
            <Switch on="On" off="Off" initialValue={true} activeColor="#ea5455" />
            <Switch on="On" off="Off" initialValue={true} activeColor="#ea5455" disabled />
            <Switch onChange={v => alert(v)} />
          </Columns>
        </StyledDiv>
        <StyledDiv>
          <h1>Checkbox</h1>
          <Columns>
            <Checkbox label="Default" />
            <Checkbox disabled label="Default disabled" />
            <Checkbox initialValue={true} label="Initial active" />
            <Checkbox initialValue={true} activeColor="#37bd02" label="Custom color" />
            <Checkbox initialValue={true} activeColor="#ea5455" disabled label="Custom color disabled" />
            <Checkbox onChange={v => alert(v)} label="onChange action" />
          </Columns>
        </StyledDiv>
      </div>
    );
  }
}

export default App;
